const Styles = theme => ({
  wrapper: {
    width: '130px'
  },

  digit: {
    width: '110px',
    textAlign: 'center',
    margin: '10px'
  }, 

  button: {
    width: '110px',
    display: 'flex',
    flexDirection: 'column',
    margin: '10px'
  }
});

export default Styles;


